<template>
	<div style="height: 100%;">
		<div v-if="complete">
			<div class="top_box">
				<div class="select"></div>
				<span>{{index}}</span><span style="color: #666666;">/{{list.length}}</span>
				<span style="margin-left:0.4rem ;">:</span>
				<span>2018/01/12</span>
				<span>22:10</span>
				<span style="margin-left: 0.3rem;color: #f21b1b;">:</span>
				<span style="color: #f21b1b;">85</span>
			</div>

		</div>
		<div v-if="!complete" style="float: left;width: 100%;height: 100%;">
			<div v-if="action==3||action==4" class="top_box">
				<div class="select" @click="show"></div>
				<span>{{TestIndex}}</span><span style="color: #666666;">/{{list.length}}</span>
				<span style="margin-left:25% ;">00:29:59</span>

				<div class="submit_box" @click="submit">
					<div class="submit">

					</div>
					<span style="margin-left: 0.1rem;"></span>
				</div>
			</div>

			<div v-if="action==1||action==2" class="top_box">
				<div class="select" @click="show"></div>
				<span>{{TestIndex}}</span><span style="color: #666666;">/{{list.length}}</span>
				<span style="margin-left:20% ;">:{{title}}</span>

				<div class="submit_box" @click="submit">
					<div class="submit">

					</div>
					<span style="margin-left: 0.1rem;"></span>
				</div>
			</div>



			<div class="result_box" v-if="action==1" id="result" style="background: #FFFFFF;">
				<div class="swiper-container">
					<div class="swiper-wrapper">
						<div v-for="(item,ItemIndex) in list" class="swiper-slide">
							<div class="top" style="float: left;width: 100%;">
								<div v-if="item.type=='1'" class="type_box">
									
								</div>
								<div v-if="item.type=='2'" class="type_box">
									
								</div>
								<span v-html="item.title"></span>
							</div>
							<div  class="question_box" style="float: left;width: 100%;">

					<div v-if="item.show" v-for="(answer,index) in item.options " class="item" @click="change(ItemIndex,index,item.type,item.id,answer.id)">
									<div v-if="!answer.bol" class="select_item">
										<i></i>
									</div>
									<div v-if="answer.bol" class="select_item right" >
											<i></i>
										</div>
									
									<div style="float: left;width: 80%;">
										<span>{{answer.title}}</span>
									</div>
									
								
									
								</div>
								
							</div>

							<div v-if="!item.show" class="box" >

								<div style="float: left;width: 100%;">
									<div v-for="(answer,index) in item.options" class="item" >
										<div v-if="answer.right==2" class="select_item right" >
											<i></i>
										</div>
										<div v-if="answer.right==1&&answer.err" class="select_item false" >
											<i></i>
										</div>
										<div v-if="answer.right==1&&answer.err==false" class="select_item" >
											<i></i>
										</div>
										
										<div style="float: left;width: 80%;">
										<span>{{answer.title}}</span>
									</div>
									</div>
									

								</div>
								<div v-show="!item.show" class="expain">
									<p>: (1000,33.33%)</p>
									<div style="overflow-y: scroll;">
										{{item.solutionDesc}}
									</div>
								</div>

							</div>

						</div>

					</div>
				</div>

			</div>


       <div class="result_box" v-if="action==3||action==4||action==2" id="result" style="background: #FFFFFF;">
				<div class="swiper-container">
					<div class="swiper-wrapper">
						<div v-for="(item,ItemIndex) in list" class="swiper-slide">
							<div class="top" style="float: left;width: 100%;">
								<div v-if="item.type=='1'" class="type_box">
									
								</div>
								<div v-if="item.type=='2'" class="type_box">
									
								</div>
								<span v-html="item.title"></span>
							</div>
							<div  class="question_box" style="float: left;width: 100%;">

					<div v-if="item.show" v-for="(answer,index) in item.options " class="item" @click="changeTest(ItemIndex,index,item.type,item.id,answer.id)">
									<div v-if="!answer.bol" class="select_item">
										<i></i>
									</div>
									<div v-if="answer.bol" class="select_item right" >
											<i></i>
										</div>
									
									<div style="float: left;width: 80%;">
										<span>{{answer.title}}</span>
									</div>
									
								
									
								</div>
								
							</div>

							<div v-if="!item.show" class="box" >

								<div style="float: left;width: 100%;">
									<div v-for="(answer,index) in item.options" class="item" >
										<div v-if="answer.right==2" class="select_item right" >
											<i></i>
										</div>
										<div v-if="answer.right==1&&answer.err" class="select_item false" >
											<i></i>
										</div>
										<div v-if="answer.right==1&&answer.err==false" class="select_item" >
											<i></i>
										</div>
										
										<div style="float: left;width: 80%;">
										<span>{{answer.title}}</span>
									</div>
									</div>
									

								</div>
								<div v-show="!item.show" class="expain">
									<p>: (1000,33.33%)</p>
									<div style="overflow-y: scroll;">
										{{item.solutionDesc}}
									</div>
								</div>

							</div>

						</div>

					</div>
				</div>

			</div>










		</div>

        





		<div class="mask" v-if="showMask" @click="maskClick">

			<div class="view_box" @click.stop="boxClick">
				<li v-for="(item,index) in list" @click.stop="goEach(index)">
					<div v-if="item.selectType=='null'" class="null">{{index+1}}</div>
					<div v-if="item.selectType=='right'||item.selectType=='notAllright'" class="right">{{index+1}}</div>
					<div v-if="item.selectType=='error'" class="error">{{index+1}}</div>
				</li>

			</div>

		</div>
        
        	<div v-for="(item,index) in many"  v-if="item==TestIndex-1&&item.show" class="bottom_btn" @click="comfirmMore">
        	
        </div>
        <div class="bottom_btn" v-if="action==2">
        	
        </div>
        
        
	</div>
</template>

<script>
	import Swiper from '../../../static/swiper.min.js'
	
	export default {

		data() {
			return {
				complete: false,
				list: [],
				showMask: false,
				action: this.$route.query.Action,
				TestIndex: 1,
				title: "",
				completeNum:0,
				galleryTop:null,
				many:[],
				type:"",
				testId:"",
				sendId:"",
				right_prob:""
			}

		},
		created() {
			var that = this
			
			if(this.action == 1) {
				this.type = this.$route.query.PracticeType
				this.getPracticeData()
				setTimeout(function() {
					that.lunbo()

				})
			}else if(this.action == 3){
				this.testId = this.$route.query.testId
			this.getExaminationData()
			
				setTimeout(function() {
					that.lunbo()

				})
				
			}else if(this.action == 2){
				
				this.testId = this.$route.query.id
				this.getExaminationData()
				setTimeout(function() {
					that.lunbo()

				})
			}
		},
		mounted() {

		},
		methods: {
			submit() {
				
				this.$popup({
					title: '',
					content: ''+this.completeNum+' ('+this.list.length+') ,'
				}).then((res) => {
					
					if(res == 'sure') {
                          if(this.completeNum==0){
                          	this.$popup.center("")
                          	
                          }else{
                          	 this.submitAjax()
                          }
                        
                         
                      
					}
				})
			},
			show() {
             
				this.showMask = true

			},
			goEach(index){
				
				this.showMask = false
				this.galleryTop.slideTo(index, 500, false);
				this.TestIndex=index+1
			},
			boxClick() {

			},
			maskClick() {

				this.showMask = false

			},
			comfirmMore(){
				var  id=this.list[this.TestIndex-1].id
				var arr=[]
				for(var i=0;i<this.list[this.TestIndex-1].options.length;i++){
					if(this.list[this.TestIndex-1].options[i].bol==true){
					arr.push(this.list[this.TestIndex-1].options[i].id)
					}
					
				}
				
				
				this.sendData(id,arr)
			},
			
			change(ItemIndex, index,type,id,ids) {
				var that = this
				if(type=="1"){
					if(this.list[ItemIndex].options[index].right == "2") {
				
					this.list[ItemIndex].options[index].bol=true
					
					
					this.list[ItemIndex].selectType="right"
					
					this.getNum()
					
				} else {
					 this.list[ItemIndex].options[index].err=true
					this.list[ItemIndex].show = false
					this.$loading("Loading")
					
					
					
					setTimeout(function() {
					that.$nextTick(() => {
					

					$('.swiper-wrapper').css("height","auto")
					$("#result").css("height","auto")
					})
					
							  that.$loading("Loading",'close')
                    
						that.list[ItemIndex].selectType="error"
					that.getNum()
					},300)

				}
				
				this.sendData(id,ids)
				
				}else{
				if(this.list[ItemIndex].options[index].right == "2"){
					this.list[ItemIndex].options[index].bol=!this.list[ItemIndex].options[index].bol
					this.list[ItemIndex].selectType="notAllright"
					this.getNum()
					
				}else{
					
					 this.list[ItemIndex].options[index].err=true
					this.list[ItemIndex].show = false
					setTimeout(function() {
					that.$nextTick(() => {
					

					$('.swiper-wrapper').css("height","auto")
					$("#result").css("height","auto")
					})
					
							  that.$loading("Loading",'close')
                    
						that.list[ItemIndex].selectType="error"
					that.getNum()
					},300)
				}
					
					
				}
				
				

			},
			
			changeTest(ItemIndex,index,type,id,ids){
				if(type=="1"){
					
                    for(var i=0;i<this.list[ItemIndex].options.length;i++){
                    	this.list[ItemIndex].options[i].bol=false
                    }
                    this.list[ItemIndex].options[index].bol=true
					
				}else{
					
					this.list[ItemIndex].options[index].bol=true
					
				}
				this.list[ItemIndex].selectType="right"
				
				this.galleryTop.slideTo(ItemIndex,1000,false)
				
				
				
				
			},
			
			
			
			
			getPracticeData() {
				var obj = {
					"s": this.$store.state.sessionId,
					"a": this.action,
					"type": this.type
				}
				this.$Api.ExamPaperDetails(obj).then((res) => {
                    this.key=res.q.key
					
					for(var i = 0; i < res.q.library.length; i++) {
						var arr=[]
						if(res.q.library[i].type=='2'){
							this.many.push(i)
						}
					for(var j=0;j<res.q.library[i].options.length;j++){
						var arrObj={
							id:res.q.library[i].options[j].id,
							right:res.q.library[i].options[j].right,
							title:res.q.library[i].options[j].title,
							err:false,
							bol:false
						}
						arr.push(arrObj)
					}
						
						var obj = {
							id: res.q.library[i].id,
							minutia: res.q.library[i].minutia,
							options: arr,
							solutionDesc: res.q.library[i].solutionDesc,
							title: res.q.library[i].title,
							show: true,
							type:res.q.library[i].type,
							selectType:"null"
						}

						this.list.push(obj)
					}
					
					this.title = this.list[0].minutia
				})
                 
			},
			sendData(id,item){
				var newobj={
					"s": this.$store.state.sessionId,
					"a":this.action,
					"key":Number(this.key),
					"id":this.sendId,
					"libraryId":id,
					"answerIds":[]
				}
				if(item instanceof Array){
					for(var i=0;i<item.length;i++){
						newobj.answerIds.push(item[i])
						
					}
				}else{
					newobj.answerIds.push(item)
				}
				this.$Api.ExamAnswerSubmit(newobj).then((res)=>{
				
					
					
				})
				
				
			},
			submitAjax(){
				
				var obj={
					"s":this.$store.state.sessionId,
					"a":this.action,
					"id":this.sendId,
					"key":Number(this.key)
				}
				this.$Api.EndExamSubmit(obj).then((res)=>{
					
					this.right_prob=res.q.right_prob
					this.$router.push({
                      	name:"Complete",
                      	query:{
                      		Action:this.action,
                      		right_prob:this.right_prob,
                      		counted:this.list.length
                      	}
                      })
					
					
				})
				
			},
			
			
			
			
			getExaminationData(){
				var obj = {
					"s": this.$store.state.sessionId,
					"a": this.action,
					"type":"",
					'id':this.testId
					
				}
				this.$Api.ExamPaperDetails(obj).then((res) =>{
					 this.key=res.q.key
					console.log(this.key)
					for(var i = 0; i < res.q.library.length; i++) {
						var arr=[]
						if(res.q.library[i].type=='2'){
							this.many.push(i)
						}
					for(var j=0;j<res.q.library[i].options.length;j++){
						var arrObj={
							id:res.q.library[i].options[j].id,
							right:res.q.library[i].options[j].right,
							title:res.q.library[i].options[j].title,
							err:false,
							bol:false
						}
						arr.push(arrObj)
					}
						
						var obj = {
							id: res.q.library[i].id,
							minutia: res.q.library[i].minutia,
							options: arr,
							solutionDesc: res.q.library[i].solutionDesc,
							title: res.q.library[i].title,
							show: true,
							type:res.q.library[i].type,
							selectType:"null"
						}

						this.list.push(obj)
					}
					
					this.title = this.list[0].minutia
				})
				
			},
			lunbo() {
				this.galleryTop=null
				var that = this
				this.galleryTop = new Swiper('.swiper-container', {
					spaceBetween: 10,
					grabCursor: true,
					initialSlide: 0,
					observer: true, //swiperswiper 
					observeParents: true, //swiperswiper 
					autoplayDisableOnInteraction: false,
					autoHeight: true,
					onSlideChangeStart: function(swiper) {
						 //slide
						that.TestIndex = swiper.activeIndex + 1
						
					}

				})
                	
			},
			getNum(){
				this.completeNum=0
				for(var i=0;i<this.list.length;i++){
					if(this.list[i].selectType!="null"){
						this.completeNum+=1
					}
					
				}
				
				
			}

		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import url("../../plugin/Swiper/swiper.min.css");
	.bottom_btn{
		width: 100%;
		height: 0.8rem;
		position: fixed;
		bottom: 0;
		text-align: center;
		line-height: 0.8rem;
		color: #FFFFFF;
		background: #FA8A12;
		font-size: 0.3rem;
	}
	
	
	
	
	
	
  .swiper-container {
    width: 100%;
    min-height:100%;
  
    .swiper-wrapper {
      width: 100%;
      min-height:100%;
    
    }
  }
  
	.select {
		width: 0.32rem;
		height: 0.32rem;
		margin-top: 0.33rem;
		margin-right: 0.1rem;
		float: left;
		background: url(../../../static/select.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.top_box {
		height: 1rem;
		line-height: 1rem;
		font-size: 0.3rem;
		padding-left: 0.3rem;
		box-sizing: border-box;
		.submit_box {
			float: right;
			margin-right: 0.3rem;
			.submit {
				margin-top: 0.33rem;
				width: 0.4rem;
				float: left;
				height: 0.38rem;
				background: url(../../../static/submit.png) no-repeat;
				background-size: 100% 100%;
			}
		}
	}
	
	.result_box {
		width: 100%;
		height: 100%;
		background: #f8f8f8;
		padding-left: 0.3rem;
		padding-top: 0.25rem;
		padding-right: 0.3rem;
		padding: 0.25rem 0.3rem;
		box-sizing: border-box;
		height: auto;
		float: left;
		margin-bottom: 0.2rem;
		.top {
			position: relative;
			line-height: 0.5rem;
			text-indent: 1.5rem;
			font-size: 0.3rem;
			.type_box {
				width: 1rem;
				height: 0.45rem;
				text-align: center;
				line-height: 0.45rem;
				color: #FA8A12;
				font-size: 0.26rem;
				border: 0.02rem solid #FA8A12;
				position: absolute;
				text-indent: 0;
			}
		}
		
		.item {
			height: 0.6rem;
			margin-top: 0.4rem;
			line-height: 0.6rem;
			.select_item {
				width: 0.55rem;
				height: 0.55rem;
				border: 0.02rem solid #CCCCCC;
				border-radius: 50%;
				float: left;
				margin-left: 0.2rem;
				position: relative;
			}
			span {
				float: left;
				font-size: 0.32rem;
				margin-left: 0.3rem;
			}
			.right {
				background: #1dacf9;
				border: none;
				i {
					width: 0.28rem;
					height: 0.2rem;
					background: url(../../../static/right.png) no-repeat;
					background-size: 100% 100%;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					z-index: 50;
				}
			}
			.false {
				background: #f21b1b;
				border: none;
				i {
					width: 0.24rem;
					height: 0.24rem;
					background: url(../../../static/false.png) no-repeat;
					background-size: 100% 100%;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					z-index: 50;
				}
			}
		}
	}
	.box{
		width: 100%;
		float: left;
	}
	.expain {
		padding: 0.3rem;
		box-sizing: border-box;
		font-size: 0.28rem;
		float: left;
		p {
			color: #fa8a12;
			margin-top: 0.1rem;
			margin-bottom: 0.3rem;
		}
	}
	
	.mask {
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.3);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		.view_box {
			width: 100%;
			position: absolute;
			height: 6.8rem;
			background: #FFFFFF;
			overflow-y: scroll;
			bottom: 0;
			padding-top: 0.2rem;
			box-sizing: border-box;
			li {
				text-align: center;
				
				width: 0.85rem;
				 height: 0.85rem;
				font-size: 0.3rem;
				float: left;
			
				list-style: none;
				border-radius: 50%;
				box-sizing: border-box;
				margin-left: 0.3rem;
				margin-bottom: 0.2rem;
				
				div{
					width: 100%;
					height: 100%;
					line-height: 0.85rem;
					border-radius: 50%;
						color: #999999;
						font-size: 0.3rem;
					border: 0.02rem solid #CCCCCC;
				}
				.error {
				background: rgba(243, 43, 43, 0.3);
				color: #f32b2b;
				border: 0.02rem solid #f32b2b;
			}
			.right {
				background: rgba(64, 185, 250, 0.3);
				border: 0.02rem solid #40b9fa;
				color: #40b9fa;
			}
			.null {
				background: #e5e6e7;
			}
				
				
				
			}
			
		}
	}
</style>


